module.exports = "<template>\r\n    <div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>1. 在动态组件上使用 keep-alive</h4>\r\n            <button\r\n                    v-for=\"tab in tabs\"\r\n                    v-bind:key=\"tab\"\r\n                    v-bind:class=\"['tab-button', { active: currentTab === tab }]\"\r\n                    v-on:click=\"currentTab = tab\"\r\n            >{{ tab }}\r\n            </button>\r\n\r\n            <keep-alive>\r\n                <component\r\n                        v-bind:is=\"currentTabComponent\"\r\n                        class=\"tab\"\r\n                ></component>\r\n            </keep-alive>\r\n        </div>\r\n\r\n\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Watch} from 'vue-property-decorator'\r\n    import $Vue from 'vue/dist/vue.js'\r\n\r\n    $Vue.component('tab-posts', {\r\n        data () {\r\n            return {\r\n                posts: [\r\n                    {\r\n                        id: 1,\r\n                        title: 'Cat Ipsum',\r\n                        content: '<p>Dont wait for the storm to pass, dance in the rain kick' +\r\n                        ' up litter decide to want nothing to do with my owner today demand to ' +\r\n                        'be let outside at once, and expect owner to wait for me as i think ' +\r\n                        'about it cat cat moo moo lick ears lick paws so make meme, make cute' +\r\n                        ' face but lick the other cats. Kitty poochy chase imaginary bugs, but ' +\r\n                        'stand in front of the computer screen. Sweet beast cat dog hate mouse eat ' +\r\n                        'string barf pillow no baths hate everything stare at guinea pigs. My left ' +\r\n                        'donut is missing, as is my right loved it, hated it, loved it, hated it' +\r\n                        ' scoot butt on the rug cat not kitten around</p>'\r\n                    },\r\n                    {\r\n                        id: 2,\r\n                        title: 'Hipster Ipsum',\r\n                        content: '<p>Bushwick blue bottle scenester helvetica ugh, meh four loko.' +\r\n                        ' Put a bird on it lumbersexual franzen shabby chic, street art knausgaard ' +\r\n                        'trust fund shaman scenester live-edge mixtape taxidermy viral yuccie succulents.' +\r\n                        ' Keytar poke bicycle rights, crucifix street art neutra air plant PBR&B hoodie' +\r\n                        ' plaid venmo. Tilde swag art party fanny pack vinyl letterpress venmo jean shorts ' +\r\n                        'offal mumblecore. Vice blog gentrify mlkshk tattooed occupy snackwave, hoodie craft' +\r\n                        ' beer next level migas 8-bit chartreuse. Trust fund food truck drinking vinegar gochujang.</p>'\r\n                    },\r\n                    {\r\n                        id: 3,\r\n                        title: 'Cupcake Ipsum',\r\n                        content: '<p>Icing dessert soufflé lollipop chocolate bar sweet' +\r\n                        ' tart cake chupa chups. Soufflé marzipan jelly beans croissant toffee' +\r\n                        ' marzipan cupcake icing fruitcake. Muffin cake pudding soufflé wafer jelly' +\r\n                        ' bear claw sesame snaps marshmallow. Marzipan soufflé croissant lemon drops gingerbread ' +\r\n                        'sugar plum lemon drops apple pie gummies. Sweet roll donut oat cake toffee cake. Liquorice' +\r\n                        ' candy macaroon toffee cookie marzipan.</p>'\r\n                    }\r\n                ],\r\n                selectedPost: null\r\n            }\r\n        },\r\n        template: `\r\n  \t<div class=\"posts-tab\">\r\n      <ul class=\"posts-sidebar\">\r\n        <li\r\n          v-for=\"post in posts\"\r\n          v-bind:key=\"post.id\"\r\n          v-bind:class=\"{ selected: post === selectedPost }\"\r\n\t\t\t\t\tv-on:click=\"selectedPost = post\"\r\n        >\r\n          {{ post.title }}\r\n        </li>\r\n      </ul>\r\n      <div class=\"selected-post-container\">\r\n      \t<div\r\n        \tv-if=\"selectedPost\"\r\n          class=\"selected-post\"\r\n        >\r\n          <h3>{{ selectedPost.title }}</h3>\r\n          <div v-html=\"selectedPost.content\"></div>\r\n        </div>\r\n        <strong v-else>\r\n          Click on a blog title to the left to view it.\r\n        </strong>\r\n      </div>\r\n    </div>\r\n  `\r\n    })\r\n\r\n    $Vue.component('tab-archive', {\r\n        template: '<div>Archive component</div>'\r\n    })\r\n    @Component({\r\n        components: {\r\n            'tab-posts': $Vue.component('tab-posts'),\r\n            'tab-archive': $Vue.component('tab-archive')\r\n        }\r\n    })\r\n    export default class KeepAliv extends Vue {\r\n        currentTab = 'Posts'\r\n        tabs = ['Posts', 'Archive']\r\n\r\n        get currentTabComponent() {\r\n            return 'tab-' + this.currentTab.toLowerCase()\r\n        }\r\n    }\r\n</script>\r\n\r\n<style>\r\n    .tab-button {\r\n        padding: 6px 10px;\r\n        border-top-left-radius: 3px;\r\n        border-top-right-radius: 3px;\r\n        border: 1px solid #ccc;\r\n        cursor: pointer;\r\n        background: #f0f0f0;\r\n        margin-bottom: -1px;\r\n        margin-right: -1px;\r\n    }\r\n\r\n    .tab-button:hover {\r\n        background: #e0e0e0;\r\n    }\r\n\r\n    .tab-button.active {\r\n        background: #e0e0e0;\r\n    }\r\n\r\n    .tab {\r\n        border: 1px solid #ccc;\r\n        padding: 10px;\r\n    }\r\n\r\n    .posts-tab {\r\n        display: flex;\r\n    }\r\n\r\n    .posts-sidebar {\r\n        max-width: 40vw;\r\n        margin: 0;\r\n        padding: 0 10px 0 0;\r\n        list-style-type: none;\r\n        border-right: 1px solid #ccc;\r\n    }\r\n\r\n    .posts-sidebar li {\r\n        white-space: nowrap;\r\n        text-overflow: ellipsis;\r\n        overflow: hidden;\r\n        cursor: pointer;\r\n    }\r\n\r\n    .posts-sidebar li:hover {\r\n        background: #eee;\r\n    }\r\n\r\n    .posts-sidebar li.selected {\r\n        background: lightblue;\r\n    }\r\n\r\n    .selected-post-container {\r\n        padding-left: 10px;\r\n    }\r\n\r\n    .selected-post > :first-child {\r\n        margin-top: 0;\r\n        padding-top: 0;\r\n    }\r\n</style>\r\n\r\n\r\n\r\n";